special {
 font-family: "special";
 font-size: large;
 color: white;

}

#wrap {
 min-height: 100%;
}

#main {
 overflow: auto;
 padding-bottom: 180px;
 /* must be same height as the footer */
}




body:before {
 content: "";
 height: 100%;
 float: left;
 width: 0;
 margin-top: -32767px;
}


@media(prefers-reduced-motion) {
 .hidden {
  transition: none;
 }
}


.hlight {
 background: linear-gradient(180deg, rgba(255, 255, 255, 0) 65%, green 65%);
 display: inline;
 margin-top: 20px;
}

.banner {
 background: repeating-linear-gradient(60deg, darkkhaki, darkolivegreen)
}

.ribbon-highlight {
 position: relative;
 margin: 0.1em;
}

.section {
 display: grid;
 place-items: center;
 align-content: center;
 min-height: 100vh;
}

.ribbon-highlight:before {
 content: "";
 z-index: -1;
 left: -0.5em;
 top: 0.1em;
 border-width: 0.5em;
 border-style: solid;
 border-color: lightgreen;
 position: absolute;
 width: 100%;
 border-left-color: transparent;
 border-right-color: transparent;
}

.circle-sketch-highlight {
 position: relative;
}